@import "../../styles//colors.css";

.bubble {
  background-color: var(--secondary);
  color: var(--dark);
  padding: 0.5rem;
  border-radius: 0.3rem;
  position: relative;
}

.bubble:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  left: -10px;
  right: auto;
  top: 0px;
  bottom: auto;
  border: 10px solid;
  border-color: var(--secondary) transparent transparent transparent;
}

.senderName {
  /* font-weight: bold; */
}

.message {
  font-size: 0.9rem;
  line-height: 1.5rem;
}

.dateTime {
  font-size: 0.6rem;
  text-align: right;
  font-style: italic;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 7rem;
  }
  h2 {
    font-size: 4rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 1.6rem;
  }
  p {
    line-height: 2.5rem;
  }
}
