.container {
  margin-left: 1rem;
  margin-right: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  flex-direction: column;
}

.pageCenter {
  width: auto;
  height: 30vh;
  align-items: center;
  justify-content: center;
  padding-bottom: 30vh;
}

.fullWidth {
  margin: 0;
  padding: 0;
  width: 100%;
}

.withPadding {
  padding-left: 1rem;
  padding-right: 1rem;
}

.fitContentToPage {
  justify-content: space-around;
}

.light {
  background-color: rgba(255, 255, 255, 1);
}

.desktopOnly {
  display: none;
}
.mobileOnly {
  display: block;
}

@media only screen and (min-width: 768px) {
  .pageCenter {
    height: 90vh;
    padding-bottom: 0;
  }
  .withPadding {
    padding-left: 4rem;
    padding-right: 4rem;
  }
  .desktopOnly {
    display: block;
  }
  .mobileOnly {
    display: none;
  }
}
