@import "../../styles//colors.css";

.input {
  font-family: "Gantari", cursive;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.label {
  font-family: "Jost", cursive;
  font-weight: 500;
  font-size: 0.9rem;
}

.field,
.textArea {
  font-family: "Gantari", cursive;
  font-size: 1rem;
  margin-top: 0.2rem;
  padding: 0.7rem 0.4rem;
  border: none;
  color: var(--black);
  border-radius: 0.2rem;
  border: 1px solid var(--secondary);
}

.field {
}

.textArea {
  height: 100px;
  resize: none;
}
