.col {
  display: flex;
  flex-direction: column;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  flex-shrink: 0;
  margin-bottom: 1rem;
  width: calc(100% - 0.6rem);
  align-items: center;
  justify-content: center;
}

.stretch {
  flex: 1;
}

.col-1,
.col-2,
.mobile-1,
.mobile-2 {
  width: calc(16.66% - 0.6rem);
}
.col-3,
.col-4,
.mobile-3,
.mobile-4 {
  width: calc(33.33% - 0.6rem);
}
.col-5,
.col-6,
.mobile-5,
.mobile-6 {
  width: calc(50% - 0.6rem);
}
.col-7,
.col-8,
.mobile-7,
.mobile-8 {
  width: calc(66.66% - 0.6rem);
}
.col-9,
.col-10,
.mobile-9,
.mobile-10 {
  width: calc(83.33% - 0.6rem);
}
.col-11,
.col-12,
.mobile-11,
.mobile-12 {
  width: calc(100% - 0.6rem);
}

.desktopOnly {
  display: none;
}
.mobileOnly {
  display: block;
}

.noMargin {
  margin: 0;
}

@media only screen and (min-width: 769px) {
  .col-1 {
    width: calc(8.33% - 0.6rem);
  }
  .col-2 {
    width: calc(16.66% - 0.6rem);
  }
  .col-3 {
    width: calc(25% - 0.6rem);
  }
  .col-4 {
    width: calc(33.33% - 0.6rem);
  }
  .col-5 {
    width: calc(41.66% - 0.6rem);
  }
  .col-6 {
    width: calc(50% - 0.6rem);
  }
  .col-7 {
    width: calc(58.33% - 0.6rem);
  }
  .col-8 {
    width: calc(66.66% - 0.6rem);
  }
  .col-9 {
    width: calc(75% - 0.6rem);
  }
  .col-10 {
    width: calc(83.33% - 0.6rem);
  }
  .col-11 {
    width: calc(91.66% - 0.6rem);
  }
  .col-12 {
    width: calc(100% - 0.6rem);
  }

  .desktopOnly {
    display: block;
  }
  .mobileOnly {
    display: none;
  }
}
