.row {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.pageSection {
  min-height: 100vh;
  margin-top: 0;
}

.left {
  align-items: flex-start;
}

.center {
  align-items: center;
}

.right {
  align-items: flex-end;
}

.reverseOnMobile {
  flex-direction: column-reverse;
}

.mobileHorizontal {
  flex-direction: row;
}

.noMargin {
  margin-top: 0;
}

.top {
  justify-content: flex-start;
}

.pageSection.top {
  padding-top: 0.5rem;
}

.bottom {
  justify-content: flex-end;
}

.verticalCenter {
  justify-content: center;
}

@media only screen and (min-width: 600px) {
}

@media only screen and (min-width: 768px) {
  .row {
    flex-direction: row;
    align-items: flex-start;
  }

  .left {
    justify-content: flex-start;
  }

  .center {
    justify-content: center;
  }

  .right {
    justify-content: flex-end;
  }
  .verticalCenter {
    align-items: center;
  }
}
