@import "../../styles//colors.css";

.modal {
  z-index: 8;
}

.background,
.content {
  position: fixed;
}

.background {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 8;
}

.content {
  border-radius: 1rem;
  top: 50%;
  right: 50%;
  z-index: 9;
  transform: translate(50%, -50%);
  display: flex;
  align-items: stretch;
  flex-direction: column;
  justify-content: space-between;
  height: 90%;
  width: 90%;
  background-color: var(--white);
}

.content .header {
  position: relative;
  padding: 1rem;
  height: 15%;
}

.content .header .title {
  font-size: 1.5rem;
}
.content .body {
  overflow-y: scroll;
  height: 100%;
  width: 90%;
  padding: 1rem;
}
.content .footer {
  margin-top: 1rem;
}

.closeButton {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
}

.closeButton > button {
  border-radius: 0.5rem;
}

@media only screen and (min-width: 769px) {
  .closeButton {
    top: 2rem;
    right: 2rem;
  }
  .content {
    width: 30%;
  }
}
