@import url("./colors.css");

h1 {
  font-size: 5rem;
  margin-top: 0.4rem;
  margin-bottom: 0.2rem;
  font-weight: bold;
  font-family: "Qwitcher Grypen", cursive;
}

h2 {
  font-size: 3rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  font-weight: 400;
  font-family: "Qwitcher Grypen", cursive;
}

h3 {
  font-size: 1.3rem;
}

h4 {
  font-size: 1rem;
}

h3,
h4 {
  font-weight: 400;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
  font-family: "Jost", cursive;
  text-transform: uppercase;
}

h1,
h2 {
  display: flex;
  align-items: center;
}

p {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  line-height: 1.4rem;
  font-weight: 400;
  font-family: "Gantari", cursive;
}

p,
h1,
h2,
h3,
h4,
h5 {
  color: inherit;
}

a {
  text-decoration: none;
  color: #e0e0e0;
}

a:hover,
a:active {
  color: #9e9e9e;
}

.text-center {
  text-align: center;
  justify-content: center;
}

.text-right {
  text-align: right;
  justify-content: flex-end;
}

.text-large {
  font-size: 5rem;
}

.text-white {
  color: var(--white);
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 7rem;
  }
  h2 {
    font-size: 4rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 1.6rem;
  }
  p {
    line-height: 2.5rem;
  }
}
