@import "../../styles//colors.css";

.button {
  border: none;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 0.5rem;
  font-family: "Jost", cursive;
}

.s {
  font-size: 0.9rem;
}

.m {
  font-size: 1.3rem;
}

.l {
  font-size: 1.5rem;
}

.icon.s {
  font-size: 1.2rem;
}

.icon.m {
  font-size: 1.5rem;
}

.icon.l {
  font-size: 2.3rem;
}

.fill {
  color: white;
}

.fill.primary {
  background-color: var(--primary);
}
.fill.primary:active {
  background-color: var(--secondary);
}

.fill.dark {
  background-color: #483e3e;
}

.transparent {
  background-color: transparent;
}

.transparent.primary {
  color: var(--primary);
}
.transparent.primary:active {
  color: var(--dark);
}

.transparent.light {
  color: var(--white);
}
.transparent.light:active {
  color: var(--light);
}

.transparent.dark {
  color: var(--dark);
}
.transparent.dark:active {
  color: #020f1e;
}

.rounded {
  border-radius: 100vh;
}

.withShadow {
  box-shadow: 0px 3px 3px -2px rgba(0, 0, 0, 0.2),
    0px 3px 4px 0px rgba(0, 0, 0, 0.14), 0px 1px 8px 0px rgba(0, 0, 0, 0.12);
}

.button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

@media only screen and (min-width: 769px) {
  .button {
    padding: 0.8rem;
  }
  .icon.s {
    font-size: 2rem;
  }

  .icon.m {
    font-size: 2.5rem;
  }

  .icon.l {
    font-size: 3rem;
  }
}
