@import "../../styles//colors.css";

.navBar {
  z-index: 6;
  display: flex;
}

.fixed {
  position: fixed;
}

.absoulte {
  position: absolute;
}

.relative {
  position: relative;
}

.row {
  flex-direction: row;
  justify-content: center;
}

.column {
  flex-direction: column;
  align-items: center;
}

.fixed.right.top,
.absolute.right.top,
.fixed.right.verticalCenter,
.absolute.right.verticalCenter,
.fixed.center.verticalCenter,
.absolute.center.verticalCenter {
  transform: translate(50%, -50%);
}

.fixed.left.top,
.absolute.left.top,
.fixed.left.verticalCenter,
.absolute.left.verticalCenter {
  transform: translate(-50%, -50%);
}

.fixed.left.bottom,
.absolute.left.bottom {
  transform: translate(-50%, 50%);
}

.fixed.right.bottom,
.absolute.right.bottom,
.fixed.center.bottom,
.absolute.center.bottom {
  transform: translate(50%, 50%);
}

.fixed.right.bottom,
.absolute.right.bottom {
  transform: translate(50%, 50%);
}

.fixed.left,
.absolute.left {
  left: 10%;
}

.fixed.right,
.absolute.right {
  right: 10%;
}

.fixed.center,
.absolute.center {
  right: 50%;
}

.fixed.verticalCenter,
.absolute.verticalCenter {
  top: 50%;
}

.fixed.top,
.absolute.top {
  top: 10%;
}
.fixed.bottom,
.absolute.bottom {
  bottom: 10%;
}

.item {
  margin: 0.5rem;
}

.item .active button {
  color: var(--primary);
  background-color: var(--secondary);
}

@media only screen and (min-width: 769px) {
  .fixed.left,
  .absolute.left {
    left: 5%;
  }

  .fixed.right,
  .absolute.right {
    right: 5%;
  }
}
